+iIR;AG1iII;EAII,kDAAA;AHyiIR;AGpiII;EACI,mBAzHW;EA0HX,mBAAA;EACA,YAAA;EACA,kDAAA;EACA,kBAAA;AHsiIR;AA/qIA;;EAEI,YAAA;AAkrIJ;AAhrIA;EACI,SAAA;EACA,iDAAA;AAmrIJ;AAhrIA;EACI,eAAA;EACA,oBAAA;EACA,gBAAA;AAmrIJ;AAhrIA;EACI,qBAAA;AAmrIJ","sourcesContent":["/** Import themes file */\r\n@import \"./app/_themes/themes.scss\";\r\n\r\nhtml,\r\nbody {\r\n    height: 100%;\r\n}\r\nbody {\r\n    margin: 0;\r\n    font-family: Roboto, \"Helvetica Neue\", sans-serif;\r\n}\r\n\r\n* {\r\n    font-size: 16px;\r\n    line-height: initial;\r\n    font-weight: 500;\r\n}\r\n\r\na {\r\n    text-decoration: none;\r\n}\r\n","/* All ready-to-use Theme Classes */\r\n\r\n@import \"../_styles/utilities.scss\";\r\n\r\n@import \"~@angular/material/theming\";\r\n@include mat-core();\r\n\r\n.golden-charade-theme {\r\n    @import \"./golden-charade.theme.scss\";\r\n\r\n    $app-primary: mat-palette($golden-charade-primary);\r\n    $app-accent: mat-palette($golden-charade-accent);\r\n\r\n    $app-theme: mat-dark-theme(\r\n        (\r\n            color: (\r\n                primary: $app-primary,\r\n                accent: $app-accent,\r\n            ),\r\n        )\r\n    );\r\n\r\n    @include angular-material-theme($app-theme);\r\n    @include color-utilities($app-theme);\r\n}\r\n\r\n.juniper-iron-theme {\r\n    @import \"./juniper-iron.theme.scss\";\r\n\r\n    $app-primary: mat-palette($juniper-iron-primary);\r\n    $app-accent: mat-palette($juniper-iron-accent);\r\n\r\n    $app-theme: mat-dark-theme(\r\n        (\r\n            color: (\r\n                primary: $app-primary,\r\n                accent: $app-accent,\r\n            ),\r\n        )\r\n    );\r\n\r\n    @include angular-material-theme($app-theme);\r\n    @include color-utilities($app-theme);\r\n}\r\n","/** \r\n################################################################\r\n######################### CSS UTILITIES ########################\r\n################################################################\r\n*/\r\n\r\n/** Import Color Utilities */\r\n@import \"./color.utilities.scss\";\r\n\r\n/** \r\n##################################################\r\n##################### GENERAL ####################\r\n##################################################\r\n*/\r\n\r\n.full-height {\r\n    height: 100%;\r\n}\r\n\r\n.full-width {\r\n    width: 100%;\r\n}\r\n\r\n.cursor-pointer {\r\n    cursor: pointer;\r\n}\r\n\r\n/** \r\n##################################################\r\n##################### PADDING ####################\r\n##################################################\r\n*/\r\n\r\n.pad-none {\r\n    padding: 0 !important;\r\n}\r\n\r\n.pad-4 {\r\n    padding: 4px;\r\n}\r\n\r\n.pad-top-4 {\r\n    padding-top: 4px;\r\n}\r\n\r\n.pad-right-4 {\r\n    padding-right: 4px;\r\n}\r\n\r\n.pad-bottom-4 {\r\n    padding-bottom: 4px;\r\n}\r\n\r\n.pad-left-4 {\r\n    padding-left: 4px;\r\n}\r\n\r\n.pad-hr-4 {\r\n    @extend .pad-right-4;\r\n    @extend .pad-left-4;\r\n}\r\n\r\n.pad-vr-4 {\r\n    @extend .pad-top-4;\r\n    @extend .pad-bottom-4;\r\n}\r\n\r\n.pad-8 {\r\n    padding: 8px;\r\n}\r\n\r\n.pad-top-8 {\r\n    padding-top: 8px;\r\n}\r\n\r\n.pad-right-8 {\r\n    padding-right: 8px;\r\n}\r\n\r\n.pad-bottom-8 {\r\n    padding-bottom: 8px;\r\n}\r\n\r\n.pad-left-8 {\r\n    padding-left: 8px;\r\n}\r\n\r\n.pad-hr-8 {\r\n    @extend .pad-right-8;\r\n    @extend .pad-left-8;\r\n}\r\n\r\n.pad-vr-8 {\r\n    @extend .pad-top-8;\r\n    @extend .pad-bottom-8;\r\n}\r\n\r\n.pad-16 {\r\n    padding: 16px;\r\n}\r\n\r\n.pad-top-16 {\r\n    padding-top: 16px;\r\n}\r\n\r\n.pad-right-16 {\r\n    padding-right: 16px;\r\n}\r\n\r\n.pad-bottom-16 {\r\n    padding-bottom: 16px;\r\n}\r\n\r\n.pad-left-16 {\r\n    padding-left: 16px;\r\n}\r\n\r\n.pad-hr-16 {\r\n    @extend .pad-right-16;\r\n    @extend .pad-left-16;\r\n}\r\n\r\n.pad-vr-16 {\r\n    @extend .pad-top-16;\r\n    @extend .pad-bottom-16;\r\n}\r\n\r\n.pad-32 {\r\n    padding: 32px;\r\n}\r\n\r\n.pad-top-32 {\r\n    padding-top: 32px;\r\n}\r\n\r\n.pad-right-32 {\r\n    padding-right: 32px;\r\n}\r\n\r\n.pad-bottom-32 {\r\n    padding-bottom: 32px;\r\n}\r\n\r\n.pad-left-32 {\r\n    padding-left: 32px;\r\n}\r\n\r\n.pad-hr-32 {\r\n    @extend .pad-right-32;\r\n    @extend .pad-left-32;\r\n}\r\n\r\n.pad-vr-32 {\r\n    @extend .pad-top-32;\r\n    @extend .pad-bottom-32;\r\n}\r\n\r\n/** \r\n##################################################\r\n##################### MARGIN #####################\r\n##################################################\r\n*/\r\n\r\n.mar-none {\r\n    margin: 0 !important;\r\n}\r\n\r\n.mar-32 {\r\n    margin: 32px;\r\n}\r\n\r\n.mar-top-32 {\r\n    margin-top: 32px;\r\n}\r\n\r\n.mar-right-32 {\r\n    margin-right: 32px;\r\n}\r\n\r\n.mar-bottom-32 {\r\n    margin-bottom: 32px;\r\n}\r\n\r\n.mar-left-32 {\r\n    margin-left: 32px;\r\n}\r\n\r\n.mar-hr-32 {\r\n    @extend .mar-right-32;\r\n    @extend .mar-left-32;\r\n}\r\n\r\n.mar-vr-32 {\r\n    @extend .mar-top-32;\r\n    @extend .mar-bottom-32;\r\n}\r\n\r\n.mar-16 {\r\n    margin: 16px;\r\n}\r\n\r\n.mar-top-16 {\r\n    margin-top: 16px;\r\n}\r\n\r\n.mar-right-16 {\r\n    margin-right: 16px;\r\n}\r\n\r\n.mar-bottom-16 {\r\n    margin-bottom: 16px;\r\n}\r\n\r\n.mar-left-16 {\r\n    margin-left: 16px;\r\n}\r\n\r\n.mar-hr-16 {\r\n    @extend .mar-right-16;\r\n    @extend .mar-left-16;\r\n}\r\n\r\n.mar-vr-16 {\r\n    @extend .mar-top-16;\r\n    @extend .mar-bottom-16;\r\n}\r\n\r\n.mar-8 {\r\n    margin: 8px;\r\n}\r\n\r\n.mar-top-8 {\r\n    margin-top: 8px;\r\n}\r\n\r\n.mar-right-8 {\r\n    margin-right: 8px;\r\n}\r\n\r\n.mar-bottom-8 {\r\n    margin-bottom: 8px;\r\n}\r\n\r\n.mar-left-8 {\r\n    margin-left: 8px;\r\n}\r\n\r\n.mar-hr-8 {\r\n    @extend .mar-right-8;\r\n    @extend .mar-left-8;\r\n}\r\n\r\n.mar-vr-8 {\r\n    @extend .mar-top-8;\r\n    @extend .mar-bottom-8;\r\n}\r\n\r\n.mar-4 {\r\n    margin: 4px;\r\n}\r\n\r\n.mar-top-4 {\r\n    margin-top: 4px;\r\n}\r\n\r\n.mar-right-4 {\r\n    margin-right: 4px;\r\n}\r\n\r\n.mar-bottom-4 {\r\n    margin-bottom: 4px;\r\n}\r\n\r\n.mar-left-4 {\r\n    margin-left: 4px;\r\n}\r\n\r\n.mar-hr-4 {\r\n    @extend .mar-right-4;\r\n    @extend .mar-left-4;\r\n}\r\n\r\n.mar-vr-4 {\r\n    @extend .mar-top-4;\r\n    @extend .mar-bottom-4;\r\n}\r\n\r\n/** \r\n##################################################\r\n###################### FLEX ######################\r\n##################################################\r\n*/\r\n\r\n.flex {\r\n    display: flex;\r\n}\r\n\r\n.flex-col {\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.flex-centered-con {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.flex-col-centered-con {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.spacer {\r\n    flex-grow: 1;\r\n}\r\n\r\n/** \r\n##################################################\r\n##################### FONTS ######################\r\n##################################################\r\n*/\r\n\r\n.font-16 {\r\n    font-size: 1rem;\r\n}\r\n\r\n.font-20 {\r\n    font-size: 1.25rem;\r\n}\r\n\r\n.font-24 {\r\n    font-size: 1.5rem;\r\n}\r\n\r\n.font-28 {\r\n    font-size: 1.75rem;\r\n}\r\n\r\n.font-32 {\r\n    font-size: 2rem;\r\n}\r\n\r\n.font-36 {\r\n    font-size: 2.25rem;\r\n}\r\n\r\n.font-50 {\r\n    font-size: 3.125rem;\r\n}\r\n\r\n.font-42 {\r\n    font-size: 2.625rem;\r\n}\r\n\r\n.font-56 {\r\n    font-size: 3.5rem;\r\n}\r\n\r\n.font-60 {\r\n    font-size: 3.75rem;\r\n    font-weight: 500;\r\n}\r\n\r\n.font-64 {\r\n    font-size: 4rem;\r\n    font-weight: 500;\r\n}\r\n\r\n/** \r\n##################################################\r\n##################### TEXT #######################\r\n##################################################\r\n*/\r\n\r\n.text-center {\r\n    text-align: center;\r\n}\r\n\r\n/** \r\n##################################################\r\n############### LINKS & BUTTONS ##################\r\n##################################################\r\n*/\r\n\r\n.nav-link {\r\n    @extend .cursor-pointer;\r\n    @extend .text-tertiary;\r\n    &:hover {\r\n        @extend .text-accent-secondary;\r\n    }\r\n}\r\n\r\n.accent-primary-link {\r\n    @extend .cursor-pointer;\r\n    @extend .text-accent-primary;\r\n    &:hover {\r\n        text-decoration: underline;\r\n    }\r\n}\r\n\r\n.accent-link {\r\n    @extend .cursor-pointer;\r\n    @extend .text-accent-link;\r\n    &:hover {\r\n        text-decoration: underline;\r\n    }\r\n}\r\n\r\n/** \r\n##################################################\r\n#################### CUSTOM ######################\r\n##################################################\r\n*/\r\n\r\n.heading-line {\r\n    height: 8px;\r\n    width: 70px;\r\n    border-radius: 10px;\r\n    @extend .bg-tertiary;\r\n}\r\n","/** \r\n##############################################################\r\n################ ADD ALL COLOR UTILITIES HERE ################\r\n#############################################################\r\n*/\r\n\r\n@mixin color-utilities($theme) {\r\n    $primary: map-get($theme, primary);\r\n    $accent: map-get($theme, accent);\r\n\r\n    $fg-primary: mat-color($primary, 500);\r\n    $fg-secondary: mat-color($primary, 800);\r\n    $fg-tertiary: mat-color($primary, 900);\r\n\r\n    $bg-primary: mat-contrast($primary, 500);\r\n    $bg-secondary: mat-contrast($primary, 800);\r\n    $bg-tertiary: mat-contrast($primary, 900);\r\n\r\n    $accent-primary: mat-color($accent, 500);\r\n    $accent-secondary: mat-contrast($accent, 500);\r\n    $accent-link: mat-color($primary, links);\r\n\r\n    body {\r\n        background: $bg-primary;\r\n    }\r\n\r\n    /** \r\n    ##################################################\r\n    #################### SCROLLBAR ###################\r\n    ################################################## \r\n    */\r\n\r\n    ::-webkit-scrollbar {\r\n        width: 10px;\r\n        border: none;\r\n        border-radius: 20px;\r\n    }\r\n\r\n    /* Track */\r\n    ::-webkit-scrollbar-track {\r\n        background: $bg-primary;\r\n        border: none;\r\n        border-radius: 20px;\r\n    }\r\n\r\n    /* Handle */\r\n    ::-webkit-scrollbar-thumb {\r\n        background: $bg-secondary;\r\n        border-radius: 20px;\r\n    }\r\n\r\n    /* Handle on hover */\r\n    ::-webkit-scrollbar-thumb:hover {\r\n        background: $bg-secondary;\r\n    }\r\n\r\n    /** \r\n    ##################################################\r\n    ################# CUSTOM CLASSES #################\r\n    ################################################## \r\n    */\r\n\r\n    // TEXT\r\n\r\n    .text-primary {\r\n        color: $fg-primary;\r\n    }\r\n\r\n    .text-secondary {\r\n        color: $fg-secondary;\r\n    }\r\n\r\n    .text-tertiary {\r\n        color: $fg-tertiary;\r\n    }\r\n\r\n    .text-accent-primary {\r\n        color: $accent-primary;\r\n    }\r\n\r\n    .text-accent-secondary {\r\n        color: $accent-secondary;\r\n    }\r\n\r\n    .text-accent-link {\r\n        color: $accent-link;\r\n    }\r\n\r\n    .text-red {\r\n        color: red;\r\n    }\r\n\r\n    // BACKGROUND\r\n\r\n    .bg-accent-primary {\r\n        background: $accent-primary;\r\n    }\r\n\r\n    .bg-primary {\r\n        background: $bg-primary;\r\n    }\r\n\r\n    .bg-secondary {\r\n        background: $bg-secondary;\r\n    }\r\n\r\n    .bg-tertiary {\r\n        background: $bg-tertiary;\r\n    }\r\n\r\n    .bg-links {\r\n        background: $accent-link;\r\n    }\r\n\r\n    // BORDER\r\n\r\n    .bdr-accent-primary {\r\n        border: 2px solid $accent-primary;\r\n    }\r\n\r\n    .bdr-fg-secondary {\r\n        border: 2px solid $fg-secondary;\r\n    }\r\n\r\n    // BUTTONS and ICONS\r\n\r\n    .theme-button {\r\n        mat-icon {\r\n            @extend .text-accent-link;\r\n        }\r\n        box-shadow: 10px 10px 6px rgba($color: $bg-primary, $alpha: 20%);\r\n    }\r\n\r\n    // CARDS\r\n\r\n    .tech-stack-card {\r\n        background: $bg-secondary;\r\n        border-radius: 50px;\r\n        border: none;\r\n        box-shadow: 10px 10px 6px rgba($color: $bg-primary, $alpha: 20%);\r\n        padding: 32px 42px;\r\n    }\r\n}\r\n","// File for which all imports are resolved and bundled. This is the entry-point for\n// the `@angular/material` theming Sass bundle. See `//src/material:theming_bundle`.\n\n// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and with